export type Product = {
  id: string;
  barcode?: string;
  sku: string;
  name: string;
  category?: string;
  unit: string;
  cost: number; // ต้นทุน
  price: number; // ราคาขาย
  stock: number;
  minStock: number;
  image?: string; // base64 ได้
  isActive: boolean;
};

export type Supplier = {
  id: string;
  name: string;
  phone?: string;
};

export type TxType = "PURCHASE_IN" | "SALE_OUT" | "ADJUST";

export type StockLedger = {
  id: string;
  productId: string;
  type: TxType;
  qty: number;
  beforeStock: number;
  afterStock: number;
  refType?: "SALE" | "PO" | "ADJUST";
  refId?: string;
  at: number;
};

export type Sale = {
  id: string;
  createdAt: number;
  paymentType: "cash" | "transfer" | "promptpay";
  discount: number;
  total: number;
};

export type SaleItem = {
  id: string;
  saleId: string;
  productId: string;
  nameAtSale: string;
  priceAtSale: number;
  costAtSale: number;
  qty: number;
  lineTotal: number;
};

const KEY_PRODUCTS = "mm_products_v1";
const KEY_LEDGER = "mm_ledger_v1";
const KEY_SALES = "mm_sales_v1";
const KEY_SALE_ITEMS = "mm_sale_items_v1";

function safeParse<T>(raw: string | null, fallback: T): T {
  if (!raw) return fallback;
  try {
    return JSON.parse(raw) as T;
  } catch {
    return fallback;
  }
}

export function loadProducts(): Product[] {
  if (typeof window === "undefined") return [];
  return safeParse<Product[]>(localStorage.getItem(KEY_PRODUCTS), []);
}

export function saveProducts(products: Product[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY_PRODUCTS, JSON.stringify(products));
}

export function loadLedger(): StockLedger[] {
  if (typeof window === "undefined") return [];
  return safeParse<StockLedger[]>(localStorage.getItem(KEY_LEDGER), []);
}

export function saveLedger(rows: StockLedger[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY_LEDGER, JSON.stringify(rows));
}

export function loadSales(): Sale[] {
  if (typeof window === "undefined") return [];
  return safeParse<Sale[]>(localStorage.getItem(KEY_SALES), []);
}

export function saveSales(rows: Sale[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY_SALES, JSON.stringify(rows));
}

export function loadSaleItems(): SaleItem[] {
  if (typeof window === "undefined") return [];
  return safeParse<SaleItem[]>(localStorage.getItem(KEY_SALE_ITEMS), []);
}

export function saveSaleItems(rows: SaleItem[]) {
  if (typeof window === "undefined") return;
  localStorage.setItem(KEY_SALE_ITEMS, JSON.stringify(rows));
}

