export function addProduct(p: Omit<Product, "id" | "stock"> & { stock?: number }) {
  const products = loadProducts();
  const next: Product = {
    id: crypto.randomUUID(),
    stock: p.stock ?? 0,
    ...p,
  };
  saveProducts([next, ...products]);
}

export function deleteProduct(id: string) {
  const products = loadProducts();
  saveProducts(products.filter((p) => p.id !== id));
}

export function adjustStock(productId: string, qtyDelta: number, reason: TxType = "ADJUST") {
  const products = loadProducts();
  const idx = products.findIndex((p) => p.id === productId);
  if (idx === -1) return;

  const before = products[idx].stock;
  const after = Math.max(0, before + qtyDelta);
  products[idx] = { ...products[idx], stock: after };
  saveProducts(products);

  const ledger = loadLedger();
  ledger.unshift({
    id: crypto.randomUUID(),
    productId,
    type: reason,
    qty: Math.abs(qtyDelta),
    beforeStock: before,
    afterStock: after,
    refType: "ADJUST",
    refId: undefined,
    at: Date.now(),
  });
  saveLedger(ledger);
}

export function findProductByBarcode(barcode: string): Product | undefined {
  const products = loadProducts();
  return products.find((p) => p.barcode === barcode && p.isActive);
}

export type CartItem = {
  productId: string;
  name: string;
  price: number;
  cost: number;
  qty: number;
};

export function checkoutSale(args: {
  cart: CartItem[];
  paymentType: Sale["paymentType"];
  discount: number;
}) {
  const { cart, paymentType, discount } = args;

  if (cart.length === 0) throw new Error("Cart is empty");

  const products = loadProducts();

  // Validate stock
  for (const item of cart) {
    const p = products.find((x) => x.id === item.productId);
    if (!p) throw new Error("Product not found");
    if (p.stock < item.qty) throw new Error(`Stock not enough: ${p.name}`);
  }

  const saleId = crypto.randomUUID();
  const subtotal = cart.reduce((sum, i) => sum + i.price * i.qty, 0);
  const total = Math.max(0, subtotal - discount);

  const sale: Sale = {
    id: saleId,
    createdAt: Date.now(),
    paymentType,
    discount,
    total,
  };

  const saleItems: SaleItem[] = cart.map((i) => ({
    id: crypto.randomUUID(),
    saleId,
    productId: i.productId,
    nameAtSale: i.name,
    priceAtSale: i.price,
    costAtSale: i.cost,
    qty: i.qty,
    lineTotal: i.price * i.qty,
  }));

  // Update stock + ledger
  const ledger = loadLedger();
  for (const item of cart) {
    const idx = products.findIndex((p) => p.id === item.productId);
    const before = products[idx].stock;
    const after = before - item.qty;
    products[idx] = { ...products[idx], stock: after };

    ledger.unshift({
      id: crypto.randomUUID(),
      productId: item.productId,
      type: "SALE_OUT",
      qty: item.qty,
      beforeStock: before,
      afterStock: after,
      refType: "SALE",
      refId: saleId,
      at: Date.now(),
    });
  }

  saveProducts(products);
  saveLedger(ledger);

  const sales = loadSales();
  sales.unshift(sale);
  saveSales(sales);

  const allSaleItems = loadSaleItems();
  saveSaleItems([...saleItems, ...allSaleItems]);

  return { sale, saleItems };
}

export function getTodaySummary() {
  const now = new Date();
  const start = new Date(now.getFullYear(), now.getMonth(), now.getDate()).getTime();
  const end = start + 24 * 60 * 60 * 1000;

  const sales = loadSales().filter((s) => s.createdAt >= start && s.createdAt < end);
  const saleItems = loadSaleItems();

  const totalSales = sales.reduce((sum, s) => sum + s.total, 0);
  const totalBills = sales.length;

  const grossProfit = sales.reduce((sum, s) => {
    const items = saleItems.filter((i) => i.saleId === s.id);
    const gp = items.reduce((acc, it) => acc + (it.priceAtSale - it.costAtSale) * it.qty, 0);
    return sum + gp;
  }, 0);

  return { totalSales, totalBills, grossProfit };
}

export function getLowStockProducts() {
  const products = loadProducts();
  return products.filter((p) => p.isActive && p.stock <= p.minStock);
}
export function exportSalesCSV() {
  const sales = loadSales();
  const items = loadSaleItems();

  const header = [
    "saleId",
    "createdAt",
    "paymentType",
    "discount",
    "total",
    "productName",
    "qty",
    "priceAtSale",
    "lineTotal",
  ].join(",");

  const rows: string[] = [header];

  for (const s of sales) {
    const it = items.filter((x) => x.saleId === s.id);
    for (const row of it) {
      rows.push(
        [
          s.id,
          new Date(s.createdAt).toISOString(),
          s.paymentType,
          s.discount,
          s.total,
          `"${row.nameAtSale.replace(/"/g, '""')}"`,
          row.qty,
          row.priceAtSale,
          row.lineTotal,
        ].join(",")
      );
    }
  }

  return rows.join("\n");
}
