"use client";

import { useEffect, useMemo, useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";

import {
  CartItem,
  checkoutSale,
  findProductByBarcode,
  loadProducts,
  seedIfEmpty,
} from "@/lib/store";

export default function POSPage() {
  const [barcode, setBarcode] = useState("");
  const [cart, setCart] = useState<CartItem[]>([]);
  const [discount, setDiscount] = useState(0);
  const [paymentType, setPaymentType] = useState<"cash" | "transfer" | "promptpay">("cash");

  useEffect(() => {
    seedIfEmpty();
    loadProducts(); // ensure
  }, []);

  const subtotal = useMemo(
    () => cart.reduce((sum, i) => sum + i.price * i.qty, 0),
    [cart]
  );
  const total = useMemo(() => Math.max(0, subtotal - discount), [subtotal, discount]);

  const addByBarcode = () => {
    const code = barcode.trim();
    if (!code) return;

    const p = findProductByBarcode(code);
    if (!p) {
      alert("ไม่พบสินค้าจากบาร์โค้ดนี้");
      return;
    }

    setCart((prev) => {
      const idx = prev.findIndex((x) => x.productId === p.id);
      if (idx >= 0) {
        const next = [...prev];
        next[idx] = { ...next[idx], qty: next[idx].qty + 1 };
        return next;
      }
      return [
        ...prev,
        {
          productId: p.id,
          name: p.name,
          price: p.price,
          cost: p.cost,
          qty: 1,
        },
      ];
    });

    setBarcode("");
  };

  return (
    <div className="space-y-6">
      <div className="flex items-end justify-between">
        <div>
          <h1 className="text-2xl font-bold">POS (ขายหน้าร้าน)</h1>
          <p className="text-sm text-slate-500">ยิงบาร์โค้ด → ตัดสต็อก → สร้างบิล</p>
        </div>
        <Badge variant="secondary">Real flow</Badge>
      </div>

      <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
        {/* LEFT */}
        <Card className="rounded-2xl">
          <CardContent className="space-y-3 p-4">
            <div className="text-sm font-semibold">ยิงบาร์โค้ด</div>
            <div className="flex gap-2">
              <Input
                placeholder="เช่น 8850001112223"
                value={barcode}
                onChange={(e) => setBarcode(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === "Enter") addByBarcode();
                }}
              />
              <Button onClick={addByBarcode}>เพิ่ม</Button>
            </div>

            <div className="text-xs text-slate-500">
              ทดสอบ: 8850001112223 / 8850003334445 / 8850007778889
            </div>

            <div className="mt-2 text-sm font-semibold">ตะกร้าสินค้า</div>
            <div className="space-y-2">
              {cart.map((i) => (
                <div
                  key={i.productId}
                  className="flex items-center justify-between rounded-xl border bg-white p-3"
                >
                  <div>
                    <div className="font-medium">{i.name}</div>
                    <div className="text-xs text-slate-500">
                      {i.price} x {i.qty} = {i.price * i.qty}
                    </div>
                  </div>

                  <div className="flex items-center gap-2">
                    <Button
                      variant="secondary"
                      onClick={() =>
                        setCart((prev) =>
                          prev.map((x) =>
                            x.productId === i.productId
                              ? { ...x, qty: Math.max(1, x.qty - 1) }
                              : x
                          )
                        )
                      }
                    >
                      -
                    </Button>
                    <div className="w-6 text-center text-sm font-semibold">{i.qty}</div>
                    <Button
                      variant="secondary"
                      onClick={() =>
                        setCart((prev) =>
                          prev.map((x) =>
                            x.productId === i.productId ? { ...x, qty: x.qty + 1 } : x
                          )
                        )
                      }
                    >
                      +
                    </Button>
                    <Button
                      variant="destructive"
                      onClick={() =>
                        setCart((prev) => prev.filter((x) => x.productId !== i.productId))
                      }
                    >
                      ลบ
                    </Button>
                  </div>
                </div>
              ))}

              {cart.length === 0 && (
                <div className="rounded-xl border bg-white p-6 text-center text-sm text-slate-500">
                  ยังไม่มีสินค้าในตะกร้า
                </div>
              )}
            </div>
          </CardContent>
        </Card>

        {/* RIGHT */}
        <Card className="rounded-2xl">
          <CardContent className="space-y-3 p-4">
            <div className="text-sm font-semibold">สรุปยอด</div>

            <div className="rounded-xl border bg-white p-4 text-sm">
              <div className="flex justify-between">
                <span className="text-slate-500">Subtotal</span>
                <span className="font-semibold">{subtotal}</span>
              </div>

              <div className="mt-2 flex items-center justify-between gap-3">
                <span className="text-slate-500">ส่วนลด</span>
                <Input
                  type="number"
                  value={discount}
                  onChange={(e) => setDiscount(Number(e.target.value))}
                  className="w-40"
                />
              </div>

              <div className="mt-3 flex justify-between text-base">
                <span className="font-semibold">Total</span>
                <span className="text-lg font-bold">{total}</span>
              </div>
            </div>

            <div className="text-sm font-semibold">วิธีชำระ</div>
            <select
              className="h-10 w-full rounded-xl border bg-white px-3 text-sm"
              value={paymentType}
              onChange={(e) => setPaymentType(e.target.value as any)}
            >
              <option value="cash">เงินสด</option>
              <option value="transfer">โอน</option>
              <option value="promptpay">PromptPay</option>
            </select>

            <Button
              className="w-full rounded-xl"
              disabled={cart.length === 0}
              onClick={() => {
                try {
                  const res = checkoutSale({
                    cart,
                    paymentType,
                    discount: Number(discount) || 0,
                  });
                  alert(`ชำระเงินสำเร็จ ✅ บิล: ${res.sale.id.slice(0, 8)}`);
                  setCart([]);
                  setDiscount(0);
                } catch (err: any) {
                  alert(err.message || "Checkout failed");
                }
              }}
            >
              ชำระเงิน / ออกบิล
            </Button>

            <Button
              variant="secondary"
              className="w-full rounded-xl"
              onClick={() => {
                setCart([]);
                setDiscount(0);
              }}
            >
              ล้างตะกร้า
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
